import Button from '@/components/buttons/Button/Button';
import Game from './Game'

export default function playerInfo() {
  //Remove this lines before production
  // new Game('Kisa').render();
  //return;
  const wrapper = document.createElement('section');
  wrapper.classList.add('modal-info');
  const head = document.createElement('h1');
  head.textContent = 'Are you ready to go on a mystical trip?';
  const name = document.createElement('h3');
  name.textContent = 'Please enter your name:';
  const form = document.createElement('input');
  form.classList.add('player-name');
  form.setAttribute('type', 'textarea');
  form.setAttribute('tabindex', '1');

  const nameBtn = 'Start Game';
  const nameSelector = 'start-button';
  const startBtn = new Button(nameBtn, nameSelector);
  const element = startBtn.createButton();

  wrapper.appendChild(head);
  wrapper.appendChild(name);
  wrapper.appendChild(form);
  wrapper.appendChild(element);

  function startGame() {
    if (form.value) {
      wrapper.classList.add('hidden');
      new Game(form.value).render();
    } else {
      form.focus();
    }
  }

  element.addEventListener('click', startGame);

  wrapper.addEventListener('keydown', (e) => {
    if(e.key === 'Enter') {
      element.classList.add('active')
    }
  })

  wrapper.addEventListener('keyup', (e) => {
    if(e.key === 'Enter') {
      element.classList.remove('active');
      startGame();
    }
  })
    
  document.body.appendChild(wrapper);
  form.focus();
}