import './style.css';

export default class Health {
  constructor(selector) {
    this.selector = selector;
    this.health = 100;
  }
  healthScale() {
    const scale = document.createElement('div');
    scale.classList.add('scale', `${this.selector}`);
    let hp = document.createElement('div');
    hp.classList.add('hp');
    scale.appendChild(hp);
    this.hp = hp;
    return scale;
  }
  damage() {
    this.health -= 20;
    this.hp.style.width = `${this.health}%`;
  }
  healing() {
    this.health +=20;
    this.hp.style.width = `${this.health}%`
  }
}