import randomBackground from '@/components/backgrounds/index';
import renderName from '@/components/players/enemy-name-rendering/index';
import buttons from '@/components/buttons/buttons-group/creating-buttons'
import TaskModal from '@/components/modals/task/task-modal'
import Character from '@/components/players/Character'
import Gameover from '../../components/modals/gameover';
import correct from '../../components/soundeffects/correct.mp3';
import fail from '../../components/soundeffects/fail.mp3';
import healing from '../../components/soundeffects/healing.mp3'
import monster_attack_song from '../../components/soundeffects/monster_attack_song.mp3'
import monster_damage_song from '../../components/soundeffects/monster_damage_song.mp3'
import player_attack_song from '../../components/soundeffects/player_attack_song.mp3'
import player_damage_song from '../../components/soundeffects/player_damage_song.mp3'
import behavior from '../../components/utils/index';

export default class Game {
  constructor(name) {
    this.name = name;
    this.wrapper = document.createElement('section');
    this.wrapper.classList.add('battle-wrapper');
       
    this.data = document.createElement('div');
    this.names = document.createElement('div');
    this.healthScales = document.createElement('div');
    this.battle = document.createElement('div')
  
    this.data.classList.add('data');
    this.names.classList.add('names');
    this.healthScales.classList.add('health-scales');
    this.battle.classList.add('battle-area');

    this.player = new Character('player-hp', this.name);
    this.playerBlock = document.querySelector('.player');
    this.monster = new Character('monster-hp', renderName());
    this.monsterBlock = document.querySelector('.monster');
    this.spells = buttons(['attack', 'healing'], 'spell-btn');
    this.spells.classList.add('spell-select');

    this.attack = this.attack.bind(this);
    this.healing = this.healing.bind(this);
    
    this.wins = 0;  

    this.correctSound = new Audio(correct);
    this.failSong = new Audio(fail);
    this.playerAttackSong = new Audio(player_attack_song);
    this.playerDamageSong = new Audio(player_damage_song);
    this.monsterAttackSong = new Audio(monster_attack_song);
    this.monsterDamageSong = new Audio(monster_damage_song);
    this.healingSong = new Audio(healing);

  }
  
  taskModalAttack() {
    this.taskModal = new TaskModal();
    this.taskModal.render();
    this.taskModal.btn.addEventListener('click', this.attack)
    document.addEventListener('keydown', this.attack)  
  }

  taskModalHealing() {
    this.taskModal = new TaskModal();
    this.taskModal.render();
    this.taskModal.btn.addEventListener('click', this.healing)
    document.addEventListener('keydown', this.healing)
  }

  removeTaskModalAttack() {
    this.taskModal.btn.removeEventListener('click', this.attack);
    document.removeEventListener('keydown', this.attack);  
    this.taskModal = this.taskModal.remove();
  }

  removeTaskModalHealing() {
    this.taskModal.btn.removeEventListener('click', this.healing);
    document.removeEventListener('keydown', this.healing);  
    this.taskModal = this.taskModal.remove();
  }

  attack(e) {
    if (e.type == 'keydown' && e.key !== 'Enter') return
    behavior(0, this.taskModal.isCorrect)
    .then(() => {
      this.correctSound.play();
      this.taskModal.correctColor();
      behavior(1000)
      .then(() => {
        this.removeTaskModalAttack();
        return behavior(1000);
      })
      .then(() => {
        document.querySelector('.player').classList.add('player-attack');
        this.playerAttackSong.play()
        return behavior(1000);
      })
      .then(() => {
        this.monster.scale.damage();
        this.monsterDamageSong.play();
        document.querySelector('.player').classList.remove('player-attack');
        document.querySelector('.monster').classList.add('monster-damage');
        if (this.monster.scale.health == 0) {
          this.wins += 1;
          randomBackground();
          this.recreateMonster();
        }
        return behavior(1000);
      })
      .then(() => {
        document.querySelector('.monster').classList.remove('monster-damage');
      })
    })
    .catch(() => {
      this.failSong.play();
      this.taskModal.failColor();
      behavior(1000)
      .then(()=> {
        this.removeTaskModalAttack();
        return behavior(1000);
      })
      .then(() => {
        document.querySelector('.monster').classList.add('monster-attack');
        this.monsterAttackSong.play()
        return behavior(1000);
      })
      .then(() => {
        this.player.scale.damage();
        this.playerDamageSong.play()
        document.querySelector('.player').classList.add('player-damage');
        document.querySelector('.monster').classList.remove('monster-attack');
        if  (this.player.scale.health == 0) {
          new Gameover(this.wins).render();
        }
        return behavior(1000);
      })
      .then(() => {
        document.querySelector('.player').classList.remove('player-damage');
      })
    })
  }

  healing(e) {
    if (e.type == 'keydown' && e.key !== 'Enter') return
    behavior(0, this.taskModal.isCorrect)
    .then(() => {
      this.correctSound.play();
      this.taskModal.correctColor();
      behavior(1000)
      .then(() => {
        this.removeTaskModalHealing();
        return behavior(1000);
      })
      .then(() => {
        document.querySelector('.player').classList.add('player-healing');
        this.player.scale.healing();
        this.healingSong.play();
        return behavior(1000);
      })
      .then(() => {
        document.querySelector('.player').classList.remove('player-healing');
      })
    })
    .catch(() => {
      this.failSong.play();
      this.taskModal.failColor();
      behavior(1000)
      .then(()=> {
        this.removeTaskModalHealing();
        return behavior(1000);
      })
      .then(() => {
        debugger
        if (this.monster.scale.health == 100) {  
          document.querySelector('.monster').classList.remove('monster-attack');                 
          document.querySelector('.player').classList.add('player-damage');
          this.player.scale.damage();
          this.monsterAttackSong.play();
          if (this.player.scale.health == 0) {
            new Gameover(this.wins).render();
          }  
        } else {
          document.querySelector('.monster').classList.add('monster-healing');
          this.monster.scale.healing();
          this.healingSong.play();
        }
        return behavior(1000);
      })
      .then(() => {
        document.querySelector('.player').classList.remove('player-damage');
        document.querySelector('.monster').classList.remove('monster-attack');
      })
    })
  }

  render() {
    document.body.appendChild(this.wrapper);
    randomBackground();
    this.names.appendChild(this.player.name);
    this.names.appendChild(this.monster.name);
    this.healthScales.appendChild(this.player.health);
    this.healthScales.appendChild(this.monster.health);
    this.battle.appendChild(this.player.renderPlayer());
    this.battle.appendChild(this.monster.renderMonster());

    this.data.appendChild(this.names);
    this.data.appendChild(this.healthScales);
    
    this.wrapper.appendChild(this.data);
    this.wrapper.appendChild(this.spells);
    this.wrapper.appendChild(this.battle);

    document.querySelector('.spell-btn').addEventListener('click', this.taskModalAttack.bind(this));
    document.querySelector('.spell-btn:nth-child(2)').addEventListener('click', this.taskModalHealing.bind(this));
    document.querySelector('.spell-btn').addEventListener('keydown', (e) => {
      if (e.key === 'Enter') {
        console.log('ddddddd')
      this.taskModalAttack();
      }
      e.stopPropagation();
    });
    document.querySelector('.spell-btn:nth-child(2)').addEventListener('keydown', (e) => {
      if (e.key === 'Enter') {
      this.taskModalHealing();
      }
      e.stopPropagation();
    });
  }

  recreateMonster() {
    this.monster.name.remove();
    this.monster.health.remove();
    this.monster.monster.remove();

    this.monster = new Character('monster-hp', renderName());
    this.names.appendChild(this.monster.name);
    this.healthScales.appendChild(this.monster.health);
    this.battle.appendChild(this.monster.renderMonster());

  }
  
}