import random from '../../randomizer/index';

export default function renderName() {
  const adjective = ['Ужасный', 'Злой', 'Трусливый', 'Огромный', 'Голодный', 'Злобный', 'Тупой', 'Уродливый']; 
  const monstr = ['Огр', 'Тролль', 'Гоблин', 'Гном', 'Людоед', 'Великан'];
  const name = ['Василий', 'Пётр', 'Игнатий', 'Виталий', 'Николай', 'Валентин', 'Тимофей'];

  let fullName = random(adjective) + ' ' + random(monstr) + ' ' + random(name);
  return fullName; 
}

