import './style.css'
import random from '../../randomizer/index';

function generateSum() {
  const a = Math.floor(Math.random() * 100) + 11;
  const b = Math.floor(Math.random() * 100) + 11;
  const c = a + b;
  return [a, b, c];
}
function generateSub() {
  const a = Math.floor(Math.random() * 100) + 11;
  const b = Math.floor(Math.random() * 100) + 11;
  const c = a - b;
  return [a, b, c];
}
function generateMul() {
  const a = Math.floor(Math.random() * 12) + 3;
  const b = Math.floor(Math.random() * 12) + 3;
  const c = a * b;
  return [a, b, c];
}
function generateDiv() {
  const a = Math.floor(Math.random() * 12) + 3;
  const b = Math.floor(Math.random() * 12) + 3;
  const c = a * b;
  return [c, b, a];
}

function showTask(s, res) {
  const task = document.createElement('div');
  task.classList.add('task');
  const question = document.createElement('h3');
  question.textContent = 'Enter the correct answer';
  const fullExp = document.createElement('div');
  fullExp.classList.add('expression');
  const expression = document.createElement('p');
  expression.textContent = `${res[0]} ${s} ${res[1]} = `
  const answer = document.createElement('input');
  answer.setAttribute('type','textarea');
  answer.setAttribute('tabindex', '1');

  fullExp.appendChild(expression);
  fullExp.appendChild(answer);

  task.appendChild(question);
  task.appendChild(fullExp);

  return {task, res, answer, type: 'math'};
}

export default function generateMath() {
  const symbols = ['+', '-', '*', '/'];
  let chosen = random(symbols);

  switch (chosen) {
    case '+': 
      return showTask(chosen, generateSum());
    case '-': 
      return showTask(chosen, generateSub());
    case '*': 
      return showTask(chosen, generateMul());
    case '/': 
      return showTask(chosen, generateDiv());
  }
}