import {vocabulary} from '../../vocabulary/index';
import './style.css';
import random from '../../randomizer/index';

export default function generateTranslate() {
  let checkingWord = random(vocabulary);
  console.log(checkingWord);

  let word = Object.keys(checkingWord);
  let res = checkingWord[word[0]];

  const task = document.createElement('div');
  task.classList.add('task-translation');
  const question = document.createElement('h3');
  question.textContent = 'Translate this word:';
  const showWord = document.createElement('p');
  showWord.classList.add('word');
  showWord.textContent = `${word[0]}`
  const answer = document.createElement('input');
  answer.setAttribute('type','textarea');
  answer.setAttribute('tabindex', '1');

  task.appendChild(question);
  task.appendChild(showWord);
  task.appendChild(answer)

  return {task, res, answer, type: 'translate'};
}